<style lang="scss" scoped>
$color-primary: #9e51ff;
$t-def: 0.3s;

section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 65px;
  margin: 50px 0;
  position: relative;
  article {
    grid-column: 2 / 3;
    position: relative;
    div {
      padding: 13px 0 15px 0;
      text-align: center;
      // position: relative;
      h2 {
        font-size: 1.1em;
        color: #666;
        font-weight: lighter;
        margin-bottom: 5px;
      }
      .btn {
        background: rgba($color: #ccc, $alpha: 0.1);
        border: 1px solid #ccc;
        color: #999;
        padding: 7px 25px;
        font-size: 0.7em;
        &:hover {
          background: $color-primary;
          border: 1px solid $color-primary;
          color: #fff;
          text-decoration: none;
        }
      }
      @media (max-width: 1024px) {
        h2 {
          font-size: 1.2em;
        }
        .show-m {
          display: block !important;
        }
      }
    }
    img {
      width: 100%;
      border-radius: 2px 2px 0 0;
    }
  }

  .lab-link {
    display: grid;
    align-items: center;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0px;
    top: 0px;
    opacity: 0;
    transition: $t-def;
    i {
      color: #fff;
      cursor: pointer;
      font-size: 4em;
      transition: $t-def;
      &:hover {
        color: rgba($color: #fff, $alpha: 0.6);
      }
    }
    &:hover {
      background: $color-primary;
      opacity: 0.9;
    }
  }
}
@media (max-width: 1024px) {
  section {
    grid-template-columns: 1fr;
    margin: 0;
    padding-bottom: 75px;
    padding-top: 100px;
    article {
      grid-column: 1 / 2;
    }
  }
}

.nav-lab {
  padding: 15px 5px;
  position: fixed;
  top: 30%;
  width: 120px;
  li {
    cursor: pointer;
    font-size: 0.8em;
    margin: 10px 0 10px 20px;
    // transition: $t-def;
    text-transform: uppercase;
    &:hover {
      font-weight: bolder;
    }
    a {
      color: rgba($color: #000000, $alpha: 0.6);
      font-size: 0.8em;
      &:hover {
        text-decoration: none;
      }
      &:active {
        font-weight: bold;
      }
    }
  }
}
@media (max-width: 1024px) {
  .nav-lab {
    position: fixed;
    width: 100%;
    top: 0px;
    z-index: 1;
    ul {
      margin: auto;
      text-align: center;
    }
    li {
      display: inline;
      cursor: pointer;
      font-size: 0.8em;
      margin: 0 15px;
      text-transform: uppercase;
      &:hover {
        font-weight: normal;
      }
      &:active {
        font-weight: normal;
      }
    }
  }
}

/*MODAL*/

.modal-frame {
  position: absolute;
  z-index: 9;
}

.modal {
  display: grid;
  align-items: center;
  background: #000;
  height: 100vh;
  left: 0px;
  position: absolute;
  position: fixed;
  top: 0px;
  width: 100%;
  z-index: 2;
  .content {
    width: 75%;
    min-height: 550px;
    margin: auto;
    position: relative;
    z-index: 5;
    iframe {
      width: 100%;
      height: 500px;
    }
    img {
      width: 100%;
    }
  }
}
@media (max-width: 800px) {
  .modal {
    .content {
      iframe {
        height: 300px;
      }
    }
  }
}
.content-design {
  font-size: 0.8em;
  text-align: center;
  text-transform: uppercase;
}
/*MODAL*/
</style>